import React, { useEffect, useState } from 'react';
import Products from '../Products/Products';
import './Home.css';

const Home = () => {
   const [products, setPorducts] = useState([]);

   useEffect(() => {
      fetch('https://whispering-stream-19575.herokuapp.com/allProducts')
         .then(res => res.json())
         .then(data => {
            setPorducts(data);
         });
   }, []);

   return (
      <div className="home-section">
         <div className="container mb-5 mt-2">
            <h1 className="text-center mt-5">WELCOME TO TECHNOLOGY SHOP</h1>
            {products.length > 0 ? (
               <div className="d-flex justify-content-center">
                  <div className="spinner-border d-none" role="status"></div>
               </div>
            ) : (
               <div className="d-flex justify-content-center">
                  <div className="spinner-border " role="status"></div>
               </div>
            )}

            <div className="row mr-4">
               {products.map(product => (
                  <Products key={product._id} product={product}></Products>
               ))}
            </div>
            <h5 className="text-center my-5 pb-5 text-dark">
               2021@ Technology Shop All rights reserved by Arnob Dhaka, Bangladesh.
            </h5>
         </div>
      </div>
   );
};

export default Home;
